html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: Arial, sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: rgba(0, 0, 0, 0.5);
  line-height: 1.5;
}

.contenedor {
  width: 90%;
  max-width: 100rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 2rem 0;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.6rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.pt-5 {
  padding-top: 5rem;
}

.pt-4 {
  padding-top: 4rem;
}

.py-3 {
  padding: 3rem 0;
}

.py-4 {
  padding: 4rem 0;
}

.my-5 {
  margin: 5rem 0;
}

.p-2 {
  padding: 2rem;
}

.text-center {
  text-align: center;
}

.bg-body {
  background: rgb(1, 4, 45);
  background: linear-gradient(0deg, rgb(1, 4, 45) 0%, rgb(63, 18, 227) 100%);
}

.titulo {
  text-align: center;
  color: #ffffff;
}

.bg-alerta {
  background-color: rgb(189, 8, 8);
}

.bg-white {
  background-color: #ffffff;
  margin-top: 5rem;
}

p.d-none {
  display: none;
}

.titulo-tecnologia {
  padding-left: 4rem;
}

ul {
  margin: 0;
  padding: 0 2rem;
  padding-left: 4rem;
  line-height: 2;
}

.header-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.header-logo__imagen {
  width: 7rem;
  height: 7rem;
}

@media (min-width: 768px) {
  .formulario {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
.formulario__grid fieldset {
  border: 0.3rem solid #ffffff;
  color: #ffffff;
  text-align: center;
  margin-bottom: 2rem;
}
.formulario__grid .formulario__legend {
  font-size: 2rem;
  text-transform: uppercase;
  padding: 1rem;
}
.formulario__grid .formulario__campo {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
}
.formulario__grid .formulario__campo .formulario__label {
  font-size: 1.8rem;
  flex-basis: 10%;
}
.formulario__grid .formulario__campo .formulario__label--apuesta {
  flex-basis: 20%;
}

.formulario__imput {
  background-color: rgba(0, 0, 0, 0.5);
  color: #ffffff;
  border: 0.1rem solid #ffffff;
  border-radius: 0.5rem;
  font-size: 1.8rem;
  flex-basis: 50%;
}

.formulario__imput:focus {
  border: 0.1rem solid lightblue;
}

.formulario__imput--apuesta,
.formulario__imput--total {
  text-align: right;
  font-weight: 700;
}

.boton {
  display: block;
  font-family: "Raleway", sans-serif;
  color: #ffffff;
  letter-spacing: 0.2rem;
  text-align: center;
  padding: 1rem 3rem;
  font-size: 1.8rem;
  text-transform: uppercase;
  font-weight: 800;
  margin-bottom: 2rem;
  border: none;
  width: 100%;
  transition: all 0.3s ease-in;
}

.boton:hover {
  cursor: pointer;
}

.boton--calcular {
  background-color: #068e04;
}

.boton--limpiar {
  background-color: rgb(189, 8, 8);
}

.boton--naranja {
  background-color: rgb(238, 57, 2);
}

.boton--calcular:hover {
  background-color: #07ac05;
}

.boton--limpiar:hover {
  background-color: #da0909;
}

.boton--naranja:hover {
  background-color: #fd4c17;
}

.mensaje__parrafo, .mensaje__alerta {
  padding: 1rem;
  color: #ffffff;
  text-align: center;
  text-transform: capitalize;
}

.footer {
  background-color: rgb(189, 8, 8);
  text-align: center;
  padding: 1rem 0;
  margin-top: 4rem;
}
.footer p {
  color: #ffffff;
  margin: 0;
}

.clear-campos.d-none {
  display: none;
}