@use 'base/variables' as v; 

@mixin  telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin  tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin  desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin  desktopXL {
    @media (min-width: v.$desktopXL) {
        @content;
    }
}

@mixin grid ($columnas, $espaciado) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}

@mixin resetear-lista {
    margin: 0;
    padding: 0 2rem;
}

@mixin boton {
    
    display: block;
    font-family: v.$fuente_boton;
    color: v.$blanco;
    letter-spacing: .2rem;
    text-align: center;
    padding: 1rem 3rem;
    font-size: 1.8rem;
    text-transform: uppercase;
    font-weight: 800;
    margin-bottom: 2rem;
    border: none;
    width: 100%;
    transition: all .3s ease-in;
    
}