@use 'base/variables' as v; 
@use 'base/mixins' as m; 

.header-logo {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.header-logo__imagen {
    width: 7rem;
    height: 7rem;
}


@include m.tablet {
    .formulario {
        @include m.grid(2, 3rem);
    }
}

.formulario__grid {
    fieldset {
        border: .3rem solid v.$blanco;
        color: v.$blanco;
        text-align: center;
        margin-bottom: 2rem;
       
    }
    .formulario__legend {
        font-size: 2rem;
        text-transform: uppercase;
        padding: 1rem ;
       
    }
    .formulario__campo {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 1rem;
        margin-bottom: 1.5rem;
        .formulario__label {
            font-size: 1.8rem;
            flex-basis: 10%;
            
        }
        .formulario__label--apuesta {
            flex-basis: 20%;
        }
    }   
}

.formulario__imput {
    background-color: v.$negro;
    color: v.$blanco;
    border: 0.1rem solid v.$blanco;
    border-radius: .5rem;
    font-size: 1.8rem;
    flex-basis: 50%;
}

.formulario__imput:focus {
    border: 0.1rem solid lightblue;
}

.formulario__imput--apuesta,
.formulario__imput--total {
    text-align: right;
    font-weight: 700;
}
.boton {
    @include m.boton;
}

.boton:hover {
    cursor: pointer;
}

.boton--calcular {
    background-color: v.$verde;
}
.boton--limpiar {
    background-color: v.$rojo;
}
.boton--naranja {
    background-color: v.$naranja;
}
.boton--calcular:hover {
    background-color: lighten(v.$verde, 6);
}
.boton--limpiar:hover {
    background-color: lighten(v.$rojo, 6);
}
.boton--naranja:hover {
    background-color: lighten(v.$naranja, 7);
}

.mensaje__parrafo, .mensaje__alerta {
    padding: 1rem;
    color: v.$blanco;
    text-align: center;
    text-transform: capitalize;
}

.footer {
    background-color: v.$rojo;
    text-align: center;
    padding: 1rem 0;
    margin-top: 4rem;
    p {
        color: v.$blanco;
        margin: 0;
    }
}

.clear-campos.d-none {
    display: none;
}

