@use 'base/variables' as v; 
@use 'base/mixins' as m; 


.pt-5 {
    padding-top: 5rem ;
}
.pt-4 {
    padding-top: 4rem ;
}

.py-3 {
    padding: 3rem 0;
}
.py-4 {
    padding: 4rem 0;
}

.my-5 {
    margin: 5rem 0;
}

.p-2 {
    padding: 2rem;
}

.text-center {
    text-align: center;
}

.bg-body {
    background: rgb(1,4,45);
    background: linear-gradient(0deg, rgba(1,4,45,1) 0%, rgba(63,18,227,1) 100%);
}

.titulo {
    text-align: center;
    color: v.$blanco;
}
.bg-alerta {
    background-color: v.$rojo;
}

.bg-white {
    background-color: v.$blanco;
    margin-top: 5rem;
}

p.d-none {
    display: none;
}
.titulo-tecnologia {
    padding-left: 4rem;    
}

ul  {
    @include m.resetear-lista;
    padding-left: 4rem;
    line-height: 2;
}
