@use 'variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}
p {
    font-size: 2rem;
    color: v.$negro;
    line-height: 1.5;
}
.contenedor {
    width: 90%;
    max-width: 100rem;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
h1 , h2 , h3 {
    margin: 2rem 0;
}
h1 {
    font-size: 3.8rem;
}
h2 {
    font-size: 3.2rem;
}
h3 {
    font-size: 2.6rem;
}
img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
    
}
